// @require '../../style/reset.scss'

import React from 'react';
import ReactDom from 'react-dom';
import Tab, { TabPanel } from '../index';
import { Demo, DemoPanel } from '../../../external/demo/index';
import Doc from './doc.md';

export default class App extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      active: '2',
      prev: -1,
      active2: '2',
      prev2: -1
    };

    this.onActive = this.onActive.bind(this);
    this.onActive2 = this.onActive2.bind(this);
    this.onBeforeActive = this.onBeforeActive.bind(this);
  }

  onActive(...param) {
    this.setState({
      active: `${param[0]}`,
      prev: param[1]
    });
  }

  onBeforeActive(next) {
    if (next === 0) {
      alert('选不中第 0 项');
      return false;
    }
  }

  onActive2(...param) {
    this.setState({
      active2: `${param[0]}`,
      prev2: param[1]
    });
  }


  render() {
    return (
      <Demo name="Tab" doc={<Doc />}>
        <DemoPanel title="默认用法">
          <Tab>
            <TabPanel tab="选项1">
              <p>面板1</p>
            </TabPanel>
            <TabPanel tab="选项2">
              <p>面板2</p>
            </TabPanel>
            <TabPanel tab="选项3">
              <p>面板3</p>
            </TabPanel>
          </Tab>
        </DemoPanel>
        <DemoPanel title="边框">
          <Tab tabType="border">
            <TabPanel tab="选项1">
              <p>面板1</p>
            </TabPanel>
            <TabPanel tab="选项2">
              <p>面板2</p>
            </TabPanel>
            <TabPanel tab="选项3">
              <p>面板3</p>
            </TabPanel>
          </Tab>
        </DemoPanel>
        <DemoPanel title="设置选中">
          <Tab active="2">
            <TabPanel tab="选项1">
              <p>面板1</p>
            </TabPanel>
            <TabPanel tab="选项2">
              <p>面板2</p>
            </TabPanel>
            <TabPanel tab="选项3">
              <p>面板3</p>
            </TabPanel>
          </Tab>
        </DemoPanel>
        <DemoPanel title="选中回调">
          <p>选中第 {this.state.active} 项，上一项是 {this.state.prev}</p>
          <Tab
            active={this.state.active}
            onActive={this.onActive}
          >
            <TabPanel tab="选项1">
              <p>面板1</p>
            </TabPanel>
            <TabPanel tab="选项2">
              <p>面板2</p>
            </TabPanel>
            <TabPanel tab="选项3">
              <p>面板3</p>
            </TabPanel>
          </Tab>
        </DemoPanel>
        <DemoPanel title="选中前回调">
          <p>选中第 {this.state.active2} 项，上一项是 {this.state.prev2}</p>
          <Tab
            active={this.state.active2}
            onBeforeActive={this.onBeforeActive}
            onActive={this.onActive2}
          >
            <TabPanel tab="选项1">
              <p>面板1</p>
            </TabPanel>
            <TabPanel tab="选项2">
              <p>面板2</p>
            </TabPanel>
            <TabPanel tab="选项3">
              <p>面板3</p>
            </TabPanel>
          </Tab>
        </DemoPanel>
      </Demo>
    );
  }
}


const demo = document.getElementById('demo');

if (demo) {
  ReactDom.render(<App />, demo);
}
